*{
  box-sizing: border-box;
  margin: 0 ;
  padding: 0;
  font-family: 'Encode Sans Expanded', sans-serif;
}

.space {
  border-radius: 50px;
    background: green;
    white-sapce: nowrap;
    padding: 10px 22px;
    color: blue;
    font-size: 16px;
    outline: none;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;

    &:hover {
        transition: all 0.2s ease-in-out;
        background: white;
        color: #010606;
    }
}